.CreateTodoButton {
    background-color: #007bff;
    border: none;
    border-radius: 50%;
    bottom: 32px;
    color: #fff;
    cursor: pointer;
    font-size: 24px;
    height: 56px;
    position: fixed;
    right: 32px;
    width: 56px;
    z-index: 1;
}

.CreateTodoButton:hover {
    background-color: #0069d9;
    
}

.CreateTodoButton:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5);
    -webkit-animation:blur-out-expand 0.2s linear both;
            animation:blur-out-expand 0.2s linear both
}


.rotate-horizontal-center{-webkit-animation:rotate-horizontal-center 0.4s;animation:rotate-horizontal-center 0.4s} @-webkit-keyframes rotate-horizontal-center{0%{-webkit-transform:rotateX(0);transform:rotateX(0)}100%{-webkit-transform:rotateX(-360deg);transform:rotateX(-360deg)}} @keyframes rotate-horizontal-center{0%{-webkit-transform:rotateX(0);transform:rotateX(0)}100%{-webkit-transform:rotateX(-360deg);transform:rotateX(-360deg)}}


.blur-out-expand{-webkit-animation:blur-out-expand 0.4s linear both;animation:blur-out-expand 0.4s linear both} @-webkit-keyframes blur-out-expand{0%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(.01px);filter:blur(.01px);} 100%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(12px);filter:blur(12px);opacity:0}} @keyframes blur-out-expand{0%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(.01px);filter:blur(.01px);} 100%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(12px);filter:blur(12px);opacity:0}}
form {
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-animation: scale-up-vertical-center 0.3s;
            animation: scale-up-vertical-center 0.3s;
}

label {
    font-size: 1.2rem;
    font-weight: 700;
    margin-bottom: 1rem;
}

textarea {
    display: block;
    width: 100%;
    min-width: 400px;
    max-width: 400px;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #007bff;
    border-radius: 5px;
    font-size: 16px;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

textarea:focus {
    outline: none;
    box-shadow: 0 0 0 1px rgba(0, 123, 255, 0.5);
}

.form-buttonContainer {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin-top: 1rem;
}

.form-button {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.form-button--cancel {
    color: #565656;
    background-color: transparent;
    border: #565656 solid 1px;
    cursor: pointer;
}

.form-button--cancel:hover, .form-button--cancel:focus {
    background-color: #565656;
    color: #fff;
}

.form-button--add {
    color: #fff;
    background-color: #007bff;
    cursor: pointer;
}

.form-button--add:hover, .form-button--add:focus {
    background-color: #0069d9;
}




/* Animation */
@-webkit-keyframes scale-up-vertical-center {
  0% {
    -webkit-transform: scaleY(.2);
            transform: scaleY(.2)
  }

  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1)
  }
}
@keyframes scale-up-vertical-center {
  0% {
    -webkit-transform: scaleY(.2);
            transform: scaleY(.2)
  }

  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1)
  }
}

@media screen and (max-width: 768px) {
    form {
        min-width: 300px;
        max-width: 300px;
    }

    textarea {
        min-width: 300px;
        max-width: 300px;
    }
}
