.ModalContainer {
  position: fixed;
  min-width: 400px;
  max-width: 400px;
  width: 100%;
  top: 50%;
  left: 50%;
  z-index: 101;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 0.5rem;
  padding: 2rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 768px) {
  .ModalContainer {
    min-width: 300px;
    max-width: 300px;
  }
}
