.TodoEmpty__p{
    margin-top: 0;

    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    color: #0a2351;
    animation:flicker 4s linear infinite both;
}

.tracking-in-contract{animation:tracking-in-contract 0.4s linear both} @keyframes tracking-in-contract{0%{letter-spacing:.6em;opacity:0}40%{opacity:.6}100%{letter-spacing:normal;opacity:1}}


.flicker{animation:flicker 1.5s linear infinite both} @keyframes flicker{0%,100%{opacity:1}41.99%{opacity:1}42%{opacity:0}43%{opacity:0}43.01%{opacity:1}47.99%{opacity:1}48%{opacity:0}49%{opacity:0}49.01%{opacity:1}}